"use client";

import { motion } from "framer-motion";
import { Laptop, ShoppingBag, Share2, Video } from "lucide-react";
import Link from "next/link";

const services = [
    {
        title: "Website Development",
        description: "High-performance, scalable websites built with modern frameworks and clean architecture.",
        icon: Laptop,
        href: "/services/website-development",
        color: "bg-blue-50 text-blue-600",
    },
    {
        title: "Ecommerce Development",
        description: "Conversion-optimized online stores that provide seamless shopping experiences.",
        icon: ShoppingBag,
        href: "/services/ecommerce-development",
        color: "bg-indigo-50 text-indigo-600",
    },
    {
        title: "Social Media Marketing",
        description: "Data-driven strategies to grow your brand presence and engage your target audience.",
        icon: Share2,
        href: "/services/social-media-marketing",
        color: "bg-purple-50 text-purple-600",
    },
    {
        title: "Video Editing",
        description: "Professional post-production that tells your story with cinematic precision.",
        icon: Video,
        href: "/services/video-editing",
        color: "bg-pink-50 text-pink-600",
    },
];

export default function ServicesOverview() {
    return (
        <section className="py-20 bg-white">
            <div className="container-custom">
                <div className="text-center max-w-3xl mx-auto mb-20">
                    <h2 className="text-4xl md:text-5xl font-black tracking-tighter mb-6">Our Core Expertise</h2>
                    <p className="text-black/60 font-medium">
                        We offer specialized digital services designed to help your business dominate the digital landscape.
                    </p>
                </div>

                <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-8">
                    {services.map((service, index) => (
                        <motion.div
                            key={service.title}
                            initial={{ opacity: 0, y: 20 }}
                            whileInView={{ opacity: 1, y: 0 }}
                            viewport={{ once: true }}
                            transition={{ delay: index * 0.1 }}
                        >
                            <Link
                                href={service.href}
                                className="group block p-8 h-full bg-white border border-gray-100 rounded-3xl hover:border-indigo-600/20 hover:shadow-2xl hover:shadow-indigo-500/10 transition-all duration-500 relative overflow-hidden"
                            >
                                <div className={`${service.color} w-16 h-16 rounded-2xl flex items-center justify-center mb-8 group-hover:scale-110 transition-transform duration-500`}>
                                    <service.icon size={28} />
                                </div>
                                <h3 className="text-xl font-bold mb-4 tracking-tight">{service.title}</h3>
                                <p className="text-black/50 text-sm leading-relaxed mb-6 font-medium">
                                    {service.description}
                                </p>
                                <div className="flex items-center text-indigo-600 text-sm font-bold opacity-0 group-hover:opacity-100 transition-opacity translate-x-[-10px] group-hover:translate-x-0 duration-300">
                                    Read more
                                    <motion.span
                                        animate={{ x: [0, 5, 0] }}
                                        transition={{ repeat: Infinity, duration: 1.5 }}
                                        className="ml-2"
                                    >
                                        →
                                    </motion.span>
                                </div>
                            </Link>
                        </motion.div>
                    ))}
                </div>
            </div>
        </section>
    );
}
